.quoteBig {
    text-align: center;
    border-radius: 25px;
    padding: 20px;
    height: 500px;
    width: 100%;
    margin: 0;
    background-color: #1fd1f9;
    background-image: linear-gradient(315deg, #1fd1f9 0%, #b621fe 74%);
  }
  
  .quoteFixed {
    height: 100%;
  }
  
  .quoteHeader {
    font-family: "Cherry Swash";
    color: white;
    font-size: 40px;
    -webkit-text-stroke-width: 2px;
    -webkit-text-stroke-color: black;
  }
  
  .quotePara {
    font-family: 'Cherry Swash', cursive;
    font-size: 25px;
    letter-spacing: 2px;
    word-spacing: 2px;
    color: white;
    border-color: black;
    font-weight: 400;
    text-decoration: none solid rgb(68, 68, 68);
    font-variant: normal;
    text-transform: none;
  
  }
  
  .quoteBtn {
    border: 2px solid #aaa;
    border-radius: 6px;
    background-color: transparent;
    cursor: pointer;
    text-align: center;
    font-size: 12px;
    padding: 4px 8px;
    margin: 14px 0;
    width: 100px;
    color: #fff;
    transition: background 200ms ease-out;
    outline: none;
  }
  
  .quoteBtn:hover {
    background-color: rgba(255, 255, 255, 0.25);
  }
  
  .quoteRemain {
    height: 100px;
    color: white;
    font-size: 20px;
    border-color: white;
    font-family: 'Cherry Swash', cursive;
    border-style: dashed;
  }